import loading from "@/common/mixin/loading.js"
import card from '@/components/common/card.vue'
import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue'
import {mapState,mapActions} from 'vuex'
export default {
	mixins:[loading],
	components:{
		card,
		uniListItem
	},	
	data() {
		return {
			
		}
	},
	methods: {
		...mapActions(['doAddTest']),
		navigate(path){
			// console.log('fg')
			if(!path){
				console.log('没有该页面')
				return;
			}else{
				// console.log('有')
			}
			uni.navigateTo({
				url: `/pages/${path}/${path}`
			});
			
		},
		openOrder(type){
			switch (type){
				case 'ps':
				 uni.navigateTo({
					url: `/pages/order/order?index=1`
				 });
				break;
				case 'sh':
				 uni.navigateTo({
					url: `/pages/order/order?index=2`
				 });
				break;
				case 'tk':
				 uni.navigateTo({
					url: `/pages/order/order?index=3`
				 });
				break;
			}
		},
		jumpIndex(){
			uni.navigateTo({
				url: '../index/index.vue'
			});
		}
		
	},
	computed:{
		...mapState(['test'])
	}
}
